import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Stange({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Инвентарь - Штанга`} />
      <TopBanner style={stangeStyle} title={'Упражнения со штангой'} />
      {posts
        .filter(post => post.node.frontmatter.filterinventory === 'stange')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const stangeStyle = {
	backgroundImage: 'url(https://img1.goodfon.ru/wallpaper/big/b/a0/gym-fitness-shtanga-1.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query StangeQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



